.resume__container {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1.875rem;
}


/* Create a squares wiith shadow */
.timeline {
    background-color: var(--container-color);
    padding: 1.875rem;
    border-radius: var(--border-radius);
    position: relative;
    box-shadow: var(--shadow);
}

/* distance between sentences*/
.timeline__item {
    position: relative;
    padding-left: 3.125rem;
    padding-bottom: 3.125rem;

}

.timeline__item:last-child {
    padding-bottom: 0;

}
/* The ::before pseudo-element is used to add a vertical line before the timeline item */
/* .timeline__item::before {
    content: '';
    width: 1px;
    height: 100%;
    background-color: var(--first-color);
    position: absolute;
    left: .25rem;
    top: 0;
    z-index: 1;
} */
/* Add icon to the line */
.timeline .icon-briefcase,
.timeline .icon-graduation {
    position: absolute;
    left: -0.4375rem;
    top: 0;
    font-size: var(--h2-font-size);
    color: var(--first-color);
    border-color: var(--container-color);
    padding: 0.4375rem 0;
    z-index: 2;
}

.timeline__date {
    color: #8b88b1;
    font-size: var(--small-font-size);
}

.timeline__title {
    font-size: var(--h3-font-size);
    margin: 0.5rem 0;

}

/* Responsive on 1024px */
@media screen and (max-width: 1024px) {
    .resume__container {
        grid-template-columns: 450px;
        justify-content: center;
        row-gap: 1.875rem;
    }

    .timeline__item::before {
        left: 2px;
    }
}


/* Responsive on 768px */
@media screen and (max-width: 768px) {
    .resume__container {
        grid-template-columns: 310px;
    }
}

/* Responsive on 350px */
@media screen and (max-width: 350px) {
    .timeline__item {
        padding-left: 1.875rem;
        padding-bottom: 1.875rem;
    }

    .timeline__item::before {
        left: 0;
    }

    .timeline .icon-briefcase,
    .timeline .icon-graduation {
        left: -9px;
    }

    .timeline__text {
        text-align: justify;
    }
        
    

}